<template>
    <transition name='fade'>
      <div class='messagePage'>
        <v-Header class="head">
          <span slot='text'>我的信息</span>
        </v-Header>
        <div class='messageScroll' v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
          <div class='messageListsCon' v-for='item in lists' @click="gotoPath(item)" :key='item.msgId'>
            <div class='messageList'>
              <div class='headImg' v-if='item.srcHeadimg ' :style="{backgroundImage:'url('+item.srcHeadimg +')'}"></div>
              <div class='listInfo'>
                <span class='name' v-text='item.srcNickname'></span>
                <span class='text' v-text='item.msgCont'></span>
                <span class='time' v-text='item.publishDate '></span>
              </div>
              <div class='listImg' v-if='item.msgImg ' :style="{backgroundImage:'url('+item.msgImg +')'}"></div>
            </div>
          </div>
          <v-loadmove :type='loadType' v-if='lists.length>0'/>
        <div class='Tips' v-if='this.loadType == "all"&&lists.length==0'>暂无消息</div>
        </div>
      </div>
    </transition>
</template>
<script>
import Header from '../../components/header';
import loadMove1 from '../../components/seeOcean/loadMore.vue';
export default {
    name:'',
    props:[],
    components: {'v-Header':Header,'v-loadmove': loadMove1},
    data(){
        return {
            lists:[],           
            lastMsgId:0,
            loading:false,
            loadType:'more'
        }
    },
    methods:{
      gotoPath(_obj){
        if(_obj.msgType==1){
          console.log('审核结果-用户编号');
        }else if(_obj.msgType==11||_obj.msgType==21||_obj.msgType==31){
          this.$router.push({path:`/personalHomePage/forumPage/forumDetailsPage/${_obj.refId}`});
        }else if(_obj.msgType==12||_obj.msgType==13||_obj.msgType==14||_obj.msgType==22||_obj.msgType==23||_obj.msgType==24||_obj.msgType==32||_obj.msgType==33||_obj.msgType==34){
          this.$router.push({path:`/productArticleDetailsPage/${_obj.refId}`});
        }
      },
      loadMore(){
        console.log(1)
        this.getList();
      },
      getList() {
        if(this.loadType == 'all'||this.loading) return;
        this.loading = true;
        this.$http.get(this.$port.MY_CONT_MSG,{
          params:{
            lastMsgId:this.lastMsgId
          }
        }).then((response) => {
          const {data} = response;
          this.loading = false;
          if(response.code == 200){
            this.lists=this.lists.concat(data);
            if(data.length>0){
              this.loadType = data.length<10?'all':'more';
              this.lastMsgId = data.pop().msgId;
            }else{
              this.loadType = 'all';
            }
          }
        })
        .catch((error) => {
          this.loading = false;
          console.log(error.response.data);
        });
			},
    },
    mounted(){
      this.getList();
    }
}
</script>

<style lang="scss" scoped>
    *{
      border: none;
      margin: 0;
      padding: 0;
    }
    .messagePage{
      width:100%;
      height:100%;
      position: absolute;
      top:0;
      left:0;
      overflow: hidden;
      background:white;
      .messageScroll{
        position: absolute;
        top:0.82rem;
        left:0;
        right:0;
        bottom:0;
        margin:auto;
        overflow-x: hidden;
        overflow-y: scroll;
        -webkit-overflow-scrolling:touch;
        .messageListsCon{
          width:100%;
          height:1.5rem;
          border-bottom: 1px solid rgb(229,229,229); 
          display:flex;
          align-items:center;
          justify-content: center;
          .messageList{
            width:7.1rem;
            height:1.2rem;
            display:flex;
            .headImg{
              width:0.9rem;
              height:0.9rem;
              background-size:cover;
              background-position:center;
              border-radius:0.9rem;
              margin-right:0.23rem;
            }
            .listImg{
              width:1.2rem;
              height:1.2rem;
              background-size:cover;
              background-position:center;
              margin-left:0.2rem;
            }
            .listInfo{
              flex:1;
              display: flex;
              flex-direction: column;
              justify-content:space-between;
              overflow: hidden;
              line-height: initial;
              .name{color:#1797c6;font-size:0.24rem;}
              .text{color:#333333;font-size:0.3rem;overflow:hidden;white-space: nowrap;
              text-overflow: ellipsis;}
              .time{color:#888888;font-size:0.24rem;}
            }
          }
        }
      }
      .Tips{
        text-align: center;
      }
    }
</style>


